.services {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.oneService {
    display: -ms-flexbox;
    display: flex;
    width: 20%;
    -ms-flex-direction: column;
        flex-direction: column;
    border: 1px solid #dfdfdf;
    padding: 5px;
    background-color: #f7f7f9;
    border-radius: 5px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}
.oneService:not(.selected):hover {
    -webkit-box-shadow:  0 0 2px #0275d8;
            box-shadow:  0 0 2px #0275d8;
}
.oneService.selected{
    border-color: #0275d8;
}
.oneService-name {
    width: 100%;
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    line-height: 2rem;
}
.oneService-price {
    line-height: 1.5rem;
}
.oneService-duration {
    line-height: 1.5rem;
}



